@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Caveat&family=Libre+Baskerville&family=Poppins:wght@100&display=swap');

*{
    margin: 0;
    font-family: "BebasNeue";
    color: #333;
}
.navBar{
    padding: 20px;
    display: flex;
    align-items: center;
    max-width: 600px;
    margin: 0 auto;
    border-bottom:1px solid #f2f2f2 ;
}
.navBar h1{
    color: slateblue;
}

.navBar .links{
    margin-left: auto;
}

.navBar a{
    margin-left: 16px;
    text-decoration: none;
    padding: 6px;
}

.navBar a:hover{
    color: slateblue;
}

.content{
    max-width: 600px;
    margin: 40px auto;
    padding: 20px;
}







.blog-preview{
    padding: 10px 16px;
    margin: 20px 0;
    border-bottom: 1px solid #fafafa;
}

.blog-preview:hover{
    box-shadow:1px 2px 5px rgba(0,0,0,0.1);
}
.blog-preview h2{
    font-size: 20px;
    color: slateblue;
    margin-bottom: 8px;
}
.blog-preview a{
    text-decoration: none;
}

.blog-details h2{
    font-size: 20px;
    color: slateblue;
    margin-bottom: 10px;
}

.blog-details div{
    margin: 20px 0;
}

.blog-details button{
    background: slateblue;
    color:white;
    border: 0;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
    margin-right: 10px;
}
.blog-details a{
    text-decoration: none;
    color: white;
}


/*Create New Blog*/
.create{
    max-width: 400px;
    margin: 0 auto;
    text-align: center;
}

.create label{
    text-align: left;
    display: block;
}

.create h2{
    font-size: 20px;
    color: slateblue;
    margin-bottom: 30px;
}

.create input,.create textarea ,.create select{
    width: 100%;
    padding: 6px 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    box-sizing: border-box;
    display: block;
}

.create button{
    background: slateblue;
    color:white;
    border: 0;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
}